import {mapState} from 'vuex';
export default {
	data() {
		return {
			isLogin:false
		};
	},
	onShow(){
		// 获取用户的登录信息
		const userInfoStr = uni.getStorageSync('userInfo');
		if(userInfoStr){
			this.isLogin=true;
		}else{
			this.isLogin=false;
		}
	},
	computed:{
		...mapState({
			cartList:state=>state.cart.cartList
		})
	}
}
